*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
} 
body{
    border: 50px solid transparent;
    border-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) 30 round;
}

.content {
    display: flex;
    flex-direction: column;
    margin-top: 50px;
}
/* BAR */
.bar{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    background-color: aliceblue;
    gap: 20px;
}

.bar img {
    width: 50px;
    cursor: pointer;
}

.bar > div{
    justify-self: center;
}

.bar button{
    border: none;
    font-size: 2rem;
    border-radius: 18px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    cursor: pointer;
    color:rgb(12, 111, 12);
}

.bar button:hover {
    scale: 0.97;
    color: rgb(101, 228, 101);
}

/* MAIN */

.main {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;
    font-size: 1.5rem;
}

.main div{
    width: 500px;
    background-color: azure;
    padding: 10px;
}

.main h1{
    font-size: 2rem;
}

/* SECOND PAGE */


